// axios
import axios from 'axios'

// axios
const serviceRequset = axios.create({
  // 
  timeout: 5000,
  // 
  // baseURL: process.env.BASE_URL
  baseURL: 'http://localhost:3001'
})

/**
 * 
 * 
 *  */
let loading = null
serviceRequset.interceptors.request.use(config => {
  // 
  const token = localStorage.getItem('token')
  // token
  if (token) {
    config.headers.Authorization = token
  }
  // loading
  loading = ''
  console.log(config)
  return config
}, err => {
  // ,
  console.log(err)
  return Promise.reject(err)
})

// 
serviceRequset.interceptors.response.use(response => {
  console.log(response)
  // loading
  if (loading) {
    loading = 'loading'
  }
  // 
  const responseCode = response.status
  // 200
  // 
  if (responseCode === 200) {
    // 
    // 1 return response
    return Promise.resolve(response)
  } else {
    console.log(response)
    return Promise.reject(response)
  }
}, err => {
  // 
  if (!err.response) {
    // 
    if (err.message.include('timeout')) {
      console.log('')
    } else {
      console.log('')
    }
  }
  // 2
  // 
  console.log(err)
  // 
  const responseCode = err.response.status
  switch (responseCode) {
    // 401
    case 401:
      // 
      break
    // 404
    case 404:
      // 
      break
    default:
      // 
      break
  }
  if (loading) {
    loading = 'loading'
  }
  // 
  console.log(err)
  return Promise.reject(err)
})

export default serviceRequset
